<template>
  <div id="app">
    <StyleEditor ref="styleEditor" :code="currentStyle"></StyleEditor>
    <ResumeEditor ref="resumeEditor" :markdown="currentMarkdown" :enableHtml="enableHtml"></ResumeEditor>
  </div>
</template>

<script type="text/ecmascript-6">
  import StyleEditor from './components/StyleEditor'
  import ResumeEditor from './components/ResumeEditor'
  import './assets/reset.css'

  export default {
    name: 'app',
    components: {
      StyleEditor,
      ResumeEditor
    },
    data() {
      return {
        interval: 0,
        currentStyle: '',
        enableHtml: false,
        fullStyle: [
          `/*
* 大家好，我是灿强。
*本项目源于：https://github.com/jirengu-inc
            /animating-resume。
向作者表示深深的敬意。

*/

/* 给所有元素加上过渡效果 */
* {
  transition: all .5s;
}
/* 设置背景颜色 */
html {
  color: rgb(222,222,222);
  background: rgb(0,43,54);
}
/* 设置边框 */
.styleEditor {
  padding: .5em;
  border: 1px solid;
  overflow: auto;
  width: 95vw;
  margin: 2.5vh 2.5vw;
  height: 90vh;
  position: fixed;
  left: 0;
  bottom: 0;
  font-size:0.8rem;
}

.styleEditor {
  height: 25vh;
}
/* 代码高亮 */
.token.selector{
  color: rgb(133,153,0);
}
.token.property{
  color: rgb(187,137,0);
}
.token.punctuation{
  color: yellow;
}
.token.function{
  color: rgb(42,161,152);
}


/* 准备一个编辑器 */
.resumeEditor{
  position: fixed;
  top: 0%; left: 0;
  padding: .5em;
  margin: 1.5vh;
  width: 95%;
  height: 70vh;
  border: 1px solid;
  background: white;
  color: #222;
  overflow: auto;
}
/* 开始写内容 */

`,`

/*调整一下板式*/

.one .header{
    width:100%;
}
.one .header .content{
    display: flex;
    justify-content: center;
    margin:0 auto;
    margin-top:1rem;
}
.one .header .h2{
    font-size:1.3rem;
    letter-spacing: 1px;
    position:relative;
    height:2rem;
}
.one .header .h2 span{
    color:red;
}
.one .header .h2::after{
    content:'';
    display: block;
    position:absolute;
    top:-0.8rem;
    right:-0.3rem;
    width:1rem;
    height:200%;
    background: #fff;
    border-left:1px solid #ccc;
    transform:rotateZ(50deg);
}
.one .header .en-h2{
    font-size:1rem;
    position:relative;
    z-index: 1;
    display: flex;
    align-items: flex-end;
    height:2rem;
    transform: translateX(-0.5rem);
}
.one .face{
    width:100%;
    margin-top:1rem;
}
.one .face .img{
    width:70%;
    margin:0 15%;
}
.one .about{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-left:9%;
}
.one .about .name{
    font-size:1.2rem;
    font-weight:bold;
    letter-spacing:5px;
    margin-top:0.8rem;
    font-family:myFirstFont;
}
.one .about .year{
    ont-size:1rem;
    font-weight:bold;
    height:1.5rem;
    margin-top:0.5rem;
}
.one .about .ul{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    height:6rem;
    width:100%;
}
.one .about li{
    font-weight:normal;
    list-style-type:none;
    font-size:1rem;
    width:50%;
    height:1.8rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.one .about span{
    font-weight:bold;
}
`,
`
.items .title{
    padding-left:9%;
    font-size:0.8rem;
    margin-bottom:0.5rem;
}
.items .item{
    margin-bottom:1rem;
    font-size:0.73rem;
    padding-left:9%;
    padding-right:2rem;
    display:flex;
    flex-direction:column;
    justify-content:center;
}
.items .item .date{
    margin-bottom:0.3rem;
}
.items .item .content{
    display:flex;
    justify-content:space-between;
}
`
,
`
.end{
    width:100%;
    display: flex;
    justify-content: center;
}
.end p{
    position:relative;
    color:#ccc;
}
.end p::before{
    content:'';
    display: block;
    width:3rem;
    height:2px;
    background: #ccc;
    position:absolute;
    top:50%;
    left:0;
    transform: translateY(-50%) translateX(-120%);
}
.end p::after{
    content:'';
    display: block;
    width:3rem;
    height:2px;
    background: #ccc;
    position:absolute;
    top:50%;
    right:0;
    transform: translateY(-50%) translateX(120%);

}
` ],
        currentMarkdown: '',
        fullMarkdown:[ `
    	<div class="one">
            <div class="header">
              <div class="content">
                  <h2 class="h2">我的<span>信息</span></h2>
                  <p class="en-h2">About Me</p>
              </div>
            </div>
            <div class="face">
                <img class="img" width="100" src="./static/img/01.png" alt=""/>
            </div>
            <div class="about">
                <p class="name">张创强</p>
                <p class="year">1999.02.25</p>
                <ul class="ul">
                    <li><span>学历：</span>大专</li>
                    <li><span>院校：</span>广东师范</li>
                    <li><span>专业：</span>计算机</li>
                    <li><span>英语：</span>12级</li>
                    <li><span>现居：</span>隆江</li>
                    <li><span>籍贯：</span>广东</li>
                </ul>
            </div>
	</div>
`,`
<div class="two">
	<div class="items">
	    <h2 class="title">其他信息</h2>
		<div class="item">
			<p class="date">2006.9-2010.7</p>
			<div class="content">
				<p>隆江蛟边小学</p>
				<p>小学生</p>
			</div>
		</div>
		<div class="item">
			<p class="date">2010.5-2012.7</p>
			<div class="content">
				<p>隆江中心小学</p>
				<p>小学生</p>
			</div>
		</div>
		<div class="item">
			<p class="date">2012.9-2015.6</p>
			<div class="content">
				<p>隆江中学</p>
				<p>中学生</p>
			</div>
		</div>
		<div class="item">
			<p class="date">2015.9-2018.6</p>
			<div class="content">
				<p>北京清华大学</p>
				<p>高中生</p>
			</div>
		</div>
		<div class="item">
			<!--<p class="date">2018.7-至今</p>-->
			<!--<div class="content">-->
				<!--<p>中国建筑</p>-->
				<!--<p>职位：长方体混泥土瞬间移动师</p>-->
			<!--</div>-->
            <p style="margin-top:0.3rem;">技术栈：Vue,Nuxt,React,ReactNative,Php,微信小程序</p>
            <p style="margin-top:0.3rem;">经验：使用Vue做在线商城，Nuxt做博客，React做简书，ReactNative做手机商城，php做博客还有博客后台，有多个中大型的小程序前端开发经验，能独立开发中大型小程序前端~</p>
		</div>
	</div>
</div>

`
,
`
<div class="end">
    <p>END</p>
</div>
`

]
      }
    },
    created() {
      let htmlWidth = document.documentElement.clientWidth || document.body.clientWidth;
      let htmlDom = document.getElementsByTagName('html')[0];
      htmlDom.style.fontSize= htmlWidth/20 + 'px';
      this.makeResume()
    },

    methods: {
      makeResume: async function () {
        await this.progressivelyShowStyle(0)  //基本代码


        await this.progressivelyShowResume(0)  //我的信息
        await this.progressivelyShowStyle(1)   //我的信息样式
        await this.progressivelyShowResume(1)  //其他信息
        await this.progressivelyShowStyle(2)   //其他信息样式
        await this.progressivelyShowResume(2)  //其他信息
        await this.progressivelyShowStyle(3)   //其他信息样式
        return;

        //await this.showHtml()

        await this.progressivelyShowStyle(2)
      },
      showHtml: function () {
        return new Promise((resolve, reject) => {
          this.enableHtml = true
          this.$nextTick(() => {
            this.$refs.resumeEditor.goTop()
          })
          resolve()
        })
      },
      progressivelyShowStyle(n) {
        return new Promise((resolve, reject) => {
          let interval = this.interval
          let showStyle = (async function () {
            let style = this.fullStyle[n]
            if (!style) { return }
            // 计算前 n 个 style 的字符总数
            let length = this.fullStyle.filter((_, index) => index <= n).map((item) => item.length).reduce((p, c) => p + c, 0)
            let prefixLength = length - style.length
            if (this.currentStyle.length < length) {
              let l = this.currentStyle.length - prefixLength
              let char = style.substring(l, l + 1) || ' '
              this.currentStyle += char
              if (style.substring(l - 1, l) === '\n' && this.$refs.styleEditor) {
                this.$nextTick(() => {
                  this.$refs.styleEditor.goBottom()
                })
              }
              setTimeout(showStyle, interval)
            } else {
              resolve()
            }
          }).bind(this)
          showStyle()
        })
      },
      progressivelyShowResume(n) {
        return new Promise((resolve, reject) => {
          //let length = this.fullMarkdown.length[n]

          let interval = this.interval
          let showResume = () => {
            let length = this.fullMarkdown.filter((_, index) => index <= n).map((item) => item.length).reduce((p, c) => p + c, 0)
            let markdown = this.fullMarkdown[n]
            if (!markdown) { return }
            let prefixLength = length - markdown.length
            if (this.currentMarkdown.length < length) {
              let l = this.currentMarkdown.length - prefixLength
              let char=markdown.substring(l, l+1)
              this.currentMarkdown +=char
                //let lastChar = this.currentMarkdown[this.currentMarkdown.length - 1]
              let prevChar = this.currentMarkdown[this.currentMarkdown.length - 2]
              if (prevChar === '\n' && this.$refs.resumeEditor) {
                this.$nextTick(() => this.$refs.resumeEditor.goBottom())
              }
              setTimeout(showResume, interval)
            } else {
              resolve()
            }
          }
          showResume()
        })
      }
    }
  }

</script>

<style scoped>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    min-height: 100vh; position: relative;
  }

  html {
    min-height: 100vh;
  }
  *{
    box-sizing: border-box;
  }
  img{
    width:70px;
  }

</style>



